$lightGrey: #f7f7f7;

@mixin useInter($size: 1em, $weight: 400) {
  font-family: "Inter", sans-serif;
  font-size: $size;
  font-weight: $weight;
}

.arrow {
  margin-right: 0.8em;
  font-size: 0.7em;
  font-family: fantasy;
  display: inline-block;
  &.rotated {
    transform: rotate(90deg);
  }
  cursor: default;
}

%AppButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  cursor: pointer;

  border-radius: 3px;
  border: 1px solid black;

  span {
    @include useInter(1em, 500);
    font-variant: all-small-caps;
    color: black;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
  }

  &:hover {
    background-color: $lightGrey;
  }

  &:active {
    background-color: white;
  }
}

.layerType {
  &.selected {
    font-weight: 600;
    color: black;
  }

  cursor: pointer;
  padding: 3px 5px 3px 5px;
}

.buttonGroup {
  display: flex;
  flex-direction: row;
}

.JSONView {
  flex: 1;
  flex-grow: 1;
  height: 400px;
  overflow: auto;
}

.addToMapBtn,
.toggleViewBtn {
  @extend %AppButton;
  padding: 1em 0.2em;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  margin-left: 0.5em;
  margin-right: 0.5em;
  @include useInter(1.1em, 500);
  width: 50%;
}

.deleteLayerBtn {
  font-size: 1.2em;
  @extend %AppButton;
  border: 0px;
  margin-left: auto;

  > span {
    padding-right: 5px;
    padding-left: 5px;
    transform: rotate(45deg);
    font-variant: none;
  }
}

.duplicateLayerBtn {
  font-size: 1em;
  @extend %AppButton;
  border: 0px;
  margin-left: auto;

  > span {
    padding-right: 5px;
    padding-left: 5px;
    //looks like a bug on windows where value of this property if not honored for this particular unicode character.
    font-variant: none;
  }
}

.copyBtn {
  position: fixed;
  @extend %AppButton;
  border: 0px;
  margin-top: 2px;
  margin-right: 20px;
  margin-left: auto;
  padding: 5px;
  right: 0px;
  background-color: $lightGrey;
  > span {
    padding: 5px;
  }
}

.layerItemBtns {
  display: flex;
  flex-direction: row;
  margin-left: auto;
}

.layerItem .placeholder {
  height: 28px;
  border: 0.5px dashed;
}

.layerItem.beingDragged {
  display: block;
}

.dropZone {
  height: 4px;
}

.dropZone.draggedOver {
  height: 20px;
  border: 0.5px dashed;
}

.layerItemMain {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.addLayerBtn {
  @extend %AppButton;
  padding: 0.2em 0.2em;
  width: 120px;
  @include useInter(1.1em, 500);
  margin-top: 10px;
  margin-bottom: 15px;
}

.symbolGalleryCtrlBtns {
  display: flex;
  flex-direction: row;
  > div {
    @extend %AppButton;
    padding: 0.5em 0.2em;
    width: 100px;
    margin: 0.5em;
    @include useInter(1.1em, 500);
  }
}

.layerPanel {
  height: calc(100vh - 300px);
  overflow: auto;
}

.pointInputContainer {
  display: flex;
  flex-direction: row;
  span {
    padding-left: 10px;
    padding-right: 10px;
  }
  .unit {
    padding-left: 0px;
  }
}

input[type="number"],
input[type="text"] {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom-width: 1px;
  width: 135px;
}

input[type="number"]::-webkit-inner-spin-button:focus,
input[type="number"]::-webkit-outer-spin-button:focus {
  opacity: 1;
}

input[type="color"] {
  border: none;
  background: none;
  width: 28px;
  height: 30px;
  cursor: pointer;
}

input:checked {
  background-color: white;
  color: black;
}

.symbolPropertyItem {
  padding-bottom: 10px;
}

.symbolPropertyItem textarea {
  width: 99%;
  height: 150px;
}

.SymbolPropertyEditing {
  & > span {
    display: block;
    padding-bottom: 15px;
    text-transform: uppercase;
  }
}

.addSymbolBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  border: dashed 1px;
  cursor: pointer;
}

.addItemOverlay {
  position: absolute;
  top: 0;
  height: 100%;
  background: rgba(255, 255, 255, 0.7);
  display: flex;
  flex-direction: column;
  width: 400px;
  max-width: 80vw;
  align-items: center;
  justify-content: center;
  text-align: center;

  > span {
    padding: 20px;
    align-self: flex-start;
  }

  textarea {
    height: 500px;
    width: 88%;
  }

  .addItemOverlayer_buttons {
    display: flex;
    width: 93%;
    div {
      @extend %AppButton;
      padding: 1em 0.2em;
      margin-top: 2em;
      margin-bottom: 0.5em;
      margin-left: 0.5em;
      margin-right: 0.5em;
      @include useInter(1.1em, 500);
      width: 50%;
    }
  }
}

.addWebStyle {
  display: flex;
  flex-direction: column;
  margin: 15px;
  span {
    font-size: 1em;
    color: black;
    word-break: break-all;
  }
  input {
    width: auto;
    margin-right: 20px;
    height: 30px;
  }
  div {
    div {
      @extend %AppButton;
      padding: 0.2em 0.2em;
      width: 120px;
      @include useInter(1.1em, 500);
      margin-top: 20px;
      margin-bottom: 15px;
    }
  }
}
