.App {
  height: 100%;
  display: flex;
}

.webmap {
  height: 100vh;
  flex: auto;
}

.symbolBuilder {
  height: 100vh;
  flex: none;
  width: 401px;
  max-width: 80vw;
  display: flex;
  flex-direction: column;
}

.symbolLayersEditor {
  display: flex;
  flex-direction: column;
  text-align: left;
  flex: 1;
  overflow: hidden;
  /* padding: 1.5em 20px; */
  font-size: 0.9em;
  color: #737373;
  text-decoration: none;
  font-weight: 400;
}

.layerPanel {
  flex-grow: 1;
  padding: 10px 10px;
}

.layerItem canvas {
  max-width: 20px;
  max-height: 20px;
  cursor: pointer;
}

.layerItem .preview{
  width: 20px;
  height: 20px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1), 0 1px 1px 0 rgba(0, 0, 0, 0.1);
}

.primitiveGallery {
  width: 100%;
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
  flex: auto;
  height: auto;
  overflow: auto;
}

.symbolBuilder_Title h1 {
  padding: 0.5em 20px;
  font-size: 0.95em;
  text-decoration: none;
  font-weight: 400;
}

.symbolBuilder hr {
  border: 0.5px solid #e7ecf4;
  margin: 0;
  width: 100%;
}

.primitiveThumbnail {
  height: auto;
  width: 64px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.primitiveThumbnail img {
  max-width: 80%;
  max-height: 80%;
}

.symbolPreview {
  height: 20vh;
  min-height: 120px;
  width: 300px;
  margin: auto;
}

.inputLabel,
.numberInputLabel {
  display: block;
  width: 100px;

  font-weight: 600;
  font-size: 0.9em;
}

.checkBoxLabel {
  display: inline;
  font-weight: 600;
  font-size: 0.9em;
}

.SymbolGallery {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.react-tabs__tab-panel {
  height: calc(100% - 60px);
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 5px 20px;
}
pre {
  text-align: left;
}

.react-tabs__tab-list {
  border-bottom: 0px solid #aaa !important;
}

.react-tabs__tab--selected {
  border-color: transparent !important;
  border-radius: 0% !important;
}

.react-tabs__tab-panel {
  padding: 0px !important;
}

.react-tabs {
  font-size: 0.95em;
  height: 100%;
}
